<template>
  <div id="tanslateForm" class="row">
    <div class="col-md-6 col-md-offset-3">
       <form v-on:submit="formSubmit" style="margin-top:8px" class="form-inline">
        <input class="form-control" type="text" v-model="textTotranslate" placeholder="输入需要翻译的内容">
        <select class="form-control" v-model="lan">
          <option value="en">英文</option>
          <option value="ja">日文</option>
          <option value="ko">韩文</option>
          <option value="ru">俄文</option>
          <option value="fr">法文</option>
        </select>
        <input type="submit" value="翻译" class="btn-primary form-control">
      </form>
    <div class="transText" style="border:1px;margin-top:20px;">
        {{translatedText}}
    </div>
    </div>
     
   
  </div>
</template>

<script>
export default {
  name: 'tanslateForm',
  data: function () {
    return {
      textTotranslate: '',
      translatedText: '',
      lan: 'en'
    }
  },
  methods: {
    formSubmit: function () {
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20170913T042148Z.c3306f8e4b3a4568.bf36e585e5b3b8b0b6d499cd6db23d8c6c5e3348&lang=' + this.lan + '&text=' + this.textTotranslate
     ).then((response) => {
       this.translatedText = response.body.text[0]
     })
    }
  }
}
</script>

<style>

</style>



